import BasePortfolioEntry from "./BasePortfolioEntry";

import cskg_logo from "../../images/CSKG/cskg_logo.png";



class CSKGPlus extends BasePortfolioEntry {
    static mypath='corequisite'
    static page_title = "CoreQuisite: Reinforcing CommonSense Knowledge Graphs with Contextual Requisites";
    static page_image = cskg_logo;
    static page_briefs = "In this work, we investigate the notion of context as the missing gem in modern commonsense resources. ";

    static page_keywords = ["Knowledge Graph", "CommonSense", "NLP", "Semantic Role Labeling", 'ConceptNet', 'Amazon Mechanical Turk']

    constructor(props) {
        super(props);
        this.research_title = CSKGPlus.page_title;
        this.research_highlights = [
            "Add the notion of spacial, temporal and status context to model commonsense knowledge graphs",
        ];
        this.research_details = [
            "TODO",
        ]
        this.images = [
            // cskg_1_img, cskg_sr_img, cskg_sz_img
        ]
        this.citations = [
            // "Ilievski F, Szekely P, Cheng J, Zhang F, Qasemi E. Consolidating Commonsense Knowledge. arXiv preprint arXiv:2006.06114. 2020 Jun 10.",
        ]
    }

}

export default CSKGPlus;


