	ADepthLR.page_briefs = "In this work, we build upon previous work on using Left-Right consistency for depth estimation. The conventional approach in such methods is using a series of hand-designed loss metrics to measure the consistency between the left-eye and right-image, which limits the capabilities of such models. In this work, we borrow from Generative Adversarial Networks (GAN) and propose a method for LR-Consistency that learns the consistency/loss function with goal of discriminating the inconsistent images.";
